* {
  box-sizing: border-box;
}
.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 100%;
  max-width: 1200px;
  grid-template-areas: "h h" "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
  grid-area: c;
  background-color: #f9f9f9;
  position: relative;
  min-height: 100vh;
}

